.intro{
    height: 100vh;
    width: 100%;
    display: flex;
    background-color: #2b2d2c;
    .left{
        display: flex;
        flex-direction: column;
        height: inherit;
        flex: 1;
        align-items: center;
        vertical-align: middle;
        justify-content: center;
        
        h1{
            font-size: 50px;
            color: white;
        }
        .subtext{
            h3{

                font-weight: 300;
                font-size: 25px;
                color: gray;
            }
        }
        .hireNow{
            cursor: pointer;
            margin-top: 50px;
            background-color: yellow;
            height: 50px;
            width: 200px;
            border-radius: 15px;
            display: flex;
            flex-direction: column;
            text-align: center;
            justify-content: center;
            h3{
                font-size: 25px;
                font-weight: 400;
            }
        }
            }
    .right{
        display: flex;
        height: inherit;
        flex-direction: column;
        flex: 1;
        overflow: hidden;
        align-items: flex-end;
        .imageContainer{
            width: 90vh;
            height:90vh;
            // background-color: #ca8a6c;
            background-color: #4e3a22;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            img{
                width: 90%;
            }
        }
        
            }
}